import { Component, OnInit, Inject } from '@angular/core';
import { MAT_DIALOG_DATA, MatDialogRef } from '@angular/material/dialog';
import { MatDialog } from '@angular/material/dialog';
import { WorkflowModalComponent } from '../../workflow-modal/workflow-modal.component';

@Component({
  selector: 'app-dia-task-info',
  templateUrl: './dia-task-info.component.html',
  styleUrls: ['./dia-task-info.component.scss']
})
export class DiaTaskInfoComponent implements OnInit {
  confirmation: any;
  viewInfo: any;

  constructor(
    public dialog: MatDialog,
    public dialogRef: MatDialogRef<DiaTaskInfoComponent>,
    @Inject(MAT_DIALOG_DATA) public data: any
  ) { }

  ngOnInit() {
    this.confirmation = false;
  }
  openConfirmation() {
    this.confirmation = true;
  }
  showInfo() {
    this.viewInfo = !this.viewInfo;
  }

  openConfig() {
    const data = [];
    const dialogRef = this.dialog.open(WorkflowModalComponent, { data });
    dialogRef.afterClosed().subscribe(res => {

    });
  }

}
